.contact {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 100px;

    .left {
        h1 {
            font-size: 50px;
        }

        h4 {
            margin-top: 30px;
            font-weight: normal;
        }
    }

    .right {
        display: flex;
        flex-direction: column;
        
        input {
            width: 300px;
            border: none;
            border-bottom: 1px solid rgb(168, 182, 181);
            background-color: inherit;
            outline: none;
            margin-bottom: 20px;

            &::placeholder {
                color: rgb(168, 182, 181);
            }
        }

        button {
            padding: 10px;
            border: none;
            background-color: blue;
            color: white;
            border-radius: 20px;
            font-weight: bold;
        }
    }
}

