.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 0;

    ul {
        display: flex;
        background-color: white;
        padding: 10px;
        border-radius: 15px;
        z-index: 2;

        li {
            margin: 0 20px;
            cursor: pointer;
        }
    }



}