@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Fira+Sans:wght@100&family=Ibarra+Real+Nova:ital@1&family=Josefin+Sans&family=Lato:wght@100&family=Montserrat:wght@400;700&family=Mouse+Memoirs&family=Poppins:wght@300&family=Righteous&family=Roboto+Mono&family=Rubik+Mono+One&family=Rubik:ital@1&family=Solitreo&display=swap');

* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  color: inherit;
}

body {
  height: 100vh;
  padding: 0 200px;
  background-color: whitesmoke;
  overflow-x: hidden;
}

::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-thumb {
  background-color: blue;
  border-radius: 6px;
}

html {
  scrollbar-width: thin;
}

body {
  scrollbar-color: blue transparent;
}

body::-webkit-scrollbar {
  width: 9px;
}

body::-webkit-scrollbar-thumb {
  background-color: blue;
  border-radius: 6px;
}
li {
  position: relative;
  cursor: pointer;
  text-decoration: none; 
}
li:after {
  content: "";
  display: block;
  position: absolute;
  right: 0;
  bottom: -3px;
  width: 0;
  height: 3px; 
  background-color: black; 
  transition: width 0.5s; 
}

li:hover:after {
  content: "";
  width: 100%;
  display: block;
  position: absolute;
  left: 0;
  bottom: -3px;
  height: 3px;
  background-color: blue; 
  transition: width 0.5s; 
}