.box {
    margin: 100px 0;    

    h1 {
        font-size: 50px;
        margin-bottom: 20px;
    }

    .section1 {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        .boxes {
            display: flex;
            align-items: center;
            width: 300px;
            padding: 10px 20px;
            box-shadow: 0 0 2px grey;
            border-radius: 5px;
            margin-bottom: 20px;

            .icons {
                margin-right: 15px;
                color: blue;
                font-size: 30px;
            }
        }
    }
}